import { request,getToken,Token } from '../../request.js'
export default {
	data() {
		return {
			baseURL:this.$api,
			info:[],
			manageList: [
				{
					name: '商家总额',
					money: '￥100.00'
				},
				{
					name: '上月总额',
					money: '￥100.00'
				},
				{
					name: 'VIP充值',
					money: '￥100.00'
				},
				{
					name: '上月预估',
					money: '￥100.00'
				}
			],
			personList: [
				{
					name: '我的经理'
				},
				{
					name: '我的商家'
				},
				{
					name: 'VIP会员'
				},
				{
					name: '普通会员'
				}
			],
			$src: this.$src
		};
	},
	onLoad(){
		this.getInfo();
	},
	methods:{
		getInfo(){
			let that = this;
			request({
				url:"index.php?s=/wap/member/apiParterCenter",
				method:"POST",
			}).then(function(res){
				if(res.data.code==1){
					that.info = res.data.info;
					that.manageList[0].money = "￥"+res.data.stat.history;
					that.manageList[1].money = "￥"+res.data.stat.last;
					that.manageList[2].money = "￥"+res.data.stat.recharge;
					that.manageList[3].money = "￥"+res.data.stat.predict;
				}
			});
		},
		// 跳转到我员工页面
		goStaff() {
			uni.navigateTo({
				url: '/pages/components/my-staff/my-staff'
			})
		},
		bindStaff(){
			uni.navigateTo({
				url:"/pages/mine/invite"
			})
		}
	}
}
